import React, {useState,useEffect} from 'react'
import { Switch, Link, useHistory} from 'react-router-dom'
import '../styles/navbar.css'
import {useAuth} from '../auth/AuthProvider'


function NavBar() {
    const {user,signOut} = useAuth()
    const [isLogged,setIsLogged] = useState(false)
    const history = useHistory()


    async function handleLogout() {
        await signOut()
        history.push('/login')
    }
    async function checkIfUserIsLoggedIn(){
        if(user){
            setIsLogged(true)
        } else {
            setIsLogged(false)
        }
    }

    useEffect(()=>{
        checkIfUserIsLoggedIn()
    },[])

    return (
        <Switch>
            <div className="navigation">
                <div className="left-nav">
                    {isLogged ? 
                    <ul className="navbits">
                        <li><Link id="nav-option" to="/app-gallery">Home</Link></li>
                        <li><Link id="nav-option" to="/games">Games</Link></li>
                        <li><Link id="nav-option" to="/upload">Upload</Link></li>
                        <li><Link id="nav-option" to="/discussions">Discussions</Link></li> 
                    </ul> : 
                    <ul className="navbits">
                        <li><Link id="nav-option" to="/app-gallery">Home</Link></li>
                        <li><Link id="nav-option" to="/games">Games</Link></li>
                        <li><Link id="nav-option" to="/discussions">Discussions</Link></li> 
                    </ul>
                    }
            </div>
                {
                // if the user is logged in, replace the Login and Register buttons with their corresponding ones.   
                isLogged ?  
                   
                        <div className="right-nav">
                             <ul className="navbits">
                                <li><Link id="nav-option" to="/dashboard">Account</Link></li>
                                <li><Link id="nav-option" onClick={handleLogout} to="">Log out</Link></li>     
                            </ul>
                        </div>  : 
                        <div className="right-nav">
                             <ul className="navbits">
                                <li><Link id="nav-option" to="/login">Login</Link></li>
                                <li><Link id="nav-option" to="/register">Register</Link></li>
                            </ul>
                        </div>
                    
                        
                    
                }                
        </div>
        </Switch>
        
    )
}
export default NavBar
